<template>
  <div class="token-details-page">
    <div class="page-header">
      <div class="container">
        <h1>Token Details</h1>
        <div class="breadcrumbs">
          <router-link to="/meme-token-launch" class="breadcrumb-link">Create</router-link>
          <span class="breadcrumb-separator">/</span>
          <router-link to="/tokens" class="breadcrumb-link">Tokens</router-link>
          <span class="breadcrumb-separator">/</span>
          <span class="breadcrumb-current">Details</span>
        </div>
      </div>
    </div>
    
    <div class="container">
      <div v-if="loading" class="loading-container">
        <div class="spinner"></div>
        <p>Loading token information...</p>
      </div>
      
      <div v-else-if="error" class="error-container">
        <div class="error-icon">!</div>
        <h2>Error Loading Token</h2>
        <p>{{ error }}</p>
        <router-link to="/tokens" class="back-button">Back to Tokens</router-link>
      </div>
      
      <div v-else-if="tokenData" class="token-content">
        <div class="token-card">
          <div class="token-header">
            <div class="token-logo">
              <img v-if="tokenData.logoUrl" :src="tokenData.logoUrl" alt="Token Logo" />
              <div v-else class="placeholder-logo">{{ tokenData.symbol }}</div>
            </div>
            <div class="token-info">
              <h2 class="token-name">{{ tokenData.name }}</h2>
              <div class="token-symbol">{{ tokenData.symbol }}</div>
              <div class="token-mint">
                <strong>Mint:</strong> 
                <span v-if="tokenData.mint">{{ tokenData.mint }}</span>
                <span v-else class="pending-message">Squad bank collection in progress before launch</span>
              </div>
              <div class="token-id"><small>Token ID: {{ tokenData.tokenId }}</small></div>
              <div class="token-links">
                <a v-if="tokenData.site" :href="tokenData.site" target="_blank" class="token-link">
                  <i class="fas fa-globe"></i> Site
                </a>
                <a v-if="tokenData.twitter" :href="`https://twitter.com/${tokenData.twitter}`" target="_blank" class="token-link">
                  <i class="fab fa-twitter"></i> Twitter
                </a>
                <a v-if="tokenData.telegram" :href="`https://t.me/${tokenData.telegram}`" target="_blank" class="token-link">
                  <i class="fab fa-telegram"></i> Telegram
                </a>
              </div>
            </div>
            <div class="token-actions">
              <div class="token-rating">
                <button 
                  @click="handleLike" 
                  class="rating-btn like-btn" 
                  :class="{ 'active': hasUserLiked }"
                >
                  <i class="fas fa-thumbs-up"></i>
                  <span>{{ tokenData.likes || 0 }}</span>
                </button>
                <button 
                  @click="handleDislike" 
                  class="rating-btn dislike-btn"
                  :class="{ 'active': hasUserDisliked }"
                >
                  <i class="fas fa-thumbs-down"></i>
                  <span>{{ tokenData.dislikes || 0 }}</span>
                </button>
              </div>
            </div>
          </div>
          
          <div class="token-details">
            <div class="token-description">
              <h3>Description</h3>
              <p>{{ tokenData.description || 'No description provided' }}</p>
            </div>
            
            <div class="token-additional-info">
              <div class="info-item">
                <span class="info-label">Creator:</span>
                <router-link :to="'/player/' + tokenData.userTag" class="info-value">{{ tokenData.userTag || 'Unknown' }}</router-link>
              </div>
              <div class="info-item">
                <span class="info-label">Created:</span>
                <span class="info-value">{{ formatDate(tokenData.createdAt) }}</span>
              </div>
              
              <div class="info-item">
                <span class="info-label">Squad Lock Time:</span>
                <span class="info-value">{{ tokenData.unlockVia || 'Not specified' }}</span>
              </div>
            </div>
          </div>
        </div>
        
        <TokenSquad 
          :tokenId="tokenId" 
          :userTag="userTag"
          @joined="handleSquadJoined"
        />
      </div>
    </div>
    
    <AppToast 
      :show="toast.show" 
      :message="toast.message" 
      :type="toast.type" 
      @close="toast.show = false" 
      :duration="toast.duration"
    />
  </div>
</template>

<script>
import axios from 'axios';
import TokenSquad from '@/components/TokenSquad.vue';
import Toast from '@/components/Toast.vue';
import store from '@/vuex';

export default {
  name: 'TokenDetailsPage',
  components: {
    TokenSquad,
    AppToast: Toast
  },
  data() {
    return {
      loading: true,
      error: '',
      tokenData: null,
      userTag: store.getters.userTag,
      toast: {
        show: false,
        message: '',
        type: 'info',
        duration: 3000
      }
    };
  },
  computed: {
    tokenId() {
      return this.$route.params.tokenId;
    },
    wallet() {
      return this.store.getters.walletAddress;
    },
    hasUserLiked() {
      if (!this.tokenData || !this.userTag) return false;
      return this.tokenData.usersLiked && this.tokenData.usersLiked.includes(this.userTag);
    },
    hasUserDisliked() {
      if (!this.tokenData || !this.userTag) return false;
      return this.tokenData.usersDislikes && this.tokenData.usersDislikes.includes(this.userTag);
    }
  },
  async created() {
    await this.loadTokenData();
  },
  methods: {
    async loadTokenData() {
      this.loading = true;
      this.error = '';
      
      try {
        if (!this.tokenId) {
          throw new Error('Token ID is required');
        }
        
        const response = await axios.get(`/token/${this.tokenId}`);
        
        if (response.data.success) {
          this.tokenData = response.data.tokenLaunch;
        } else {
          throw new Error(response.data.message || 'Failed to load token data');
        }
      } catch (error) {
        console.error('Error loading token data:', error);
        this.error = error.response?.data?.message || error.message || 'Error loading token data';
      } finally {
        this.loading = false;
      }
    },
    
    async handleLike() {
      if (!this.userTag) {
        this.showToast('Please login to like this token', 'error');
        return;
      }
      
      try {
        // Если пользователь уже поставил дизлайк, удаляем его
        if (this.hasUserDisliked) {
          await this.removeDislike();
        }
        
        // Если пользователь уже поставил лайк, удаляем его
        if (this.hasUserLiked) {
          await this.removeLike();
          return;
        }
        
        // Добавляем лайк
        const response = await axios.post(`/token/${this.tokenId}/like`, { userTag: this.userTag });
        
        if (response.data.success) {
          // Обновляем данные токена
          this.tokenData = response.data.tokenLaunch;
          this.showToast('You liked this token!', 'success');
        } else {
          throw new Error(response.data.message || 'Failed to like token');
        }
      } catch (error) {
        console.error('Error liking token:', error);
        this.showToast(error.response?.data?.message || error.message || 'Error liking token', 'error');
      }
    },
    
    async handleDislike() {
      if (!this.userTag) {
        this.showToast('Please login to dislike this token', 'error');
        return;
      }
      
      try {
        // Если пользователь уже поставил лайк, удаляем его
        if (this.hasUserLiked) {
          await this.removeLike();
        }
        
        // Если пользователь уже поставил дизлайк, удаляем его
        if (this.hasUserDisliked) {
          await this.removeDislike();
          return;
        }
        
        // Добавляем дизлайк
        const response = await axios.post(`/token/${this.tokenId}/dislike`, { userTag: this.userTag });
        
        if (response.data.success) {
          // Обновляем данные токена
          this.tokenData = response.data.tokenLaunch;
          this.showToast('You disliked this token', 'info');
        } else {
          throw new Error(response.data.message || 'Failed to dislike token');
        }
      } catch (error) {
        console.error('Error disliking token:', error);
        this.showToast(error.response?.data?.message || error.message || 'Error disliking token', 'error');
      }
    },
    
    async removeLike() {
      try {
        const response = await axios.post(`/token/${this.tokenId}/unlike`, { userTag: this.userTag });
        
        if (response.data.success) {
          // Обновляем данные токена
          this.tokenData = response.data.tokenLaunch;
          this.showToast('Like removed', 'info');
        } else {
          throw new Error(response.data.message || 'Failed to remove like');
        }
      } catch (error) {
        console.error('Error removing like:', error);
        this.showToast(error.response?.data?.message || error.message || 'Error removing like', 'error');
      }
    },
    
    async removeDislike() {
      try {
        const response = await axios.post(`/token/${this.tokenId}/undislike`, { userTag: this.userTag });
        
        if (response.data.success) {
          // Обновляем данные токена
          this.tokenData = response.data.tokenLaunch;
          this.showToast('Dislike removed', 'info');
        } else {
          throw new Error(response.data.message || 'Failed to remove dislike');
        }
      } catch (error) {
        console.error('Error removing dislike:', error);
        this.showToast(error.response?.data?.message || error.message || 'Error removing dislike', 'error');
      }
    },
    
    formatDate(dateString) {
      if (!dateString) return 'N/A';
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', { 
        year: 'numeric', 
        month: 'short', 
        day: 'numeric',
        hour: '2-digit',
        minute: '2-digit'
      });
    },
    
    showToast(message, type = 'info') {
      this.toast.show = true;
      this.toast.message = message;
      this.toast.type = type;
      this.toast.duration = 3000;
    },
    
    handleSquadJoined(squadMember) {
      // Можно добавить уведомление или другую логику после успешного присоединения к Squad
      console.log('Successfully joined squad:', squadMember);
      this.showToast('Successfully joined the squad!', 'success');
    }
  }
}
</script>

